 Browser die „userbezogenen“ Informationen an unsere Seite zurück. Dank der Cookies weiß unsere Webseite, wer Sie sind und bietet Ihnen die Einstellung, die Sie gewohnt sind. In einigen Browsern hat jedes Cookie eine eigene Datei, in anderen wie beispielsweise Firefox sind alle Cookies in einer einzigen Datei gespeichert.")]),_c('p',[_vm._v("Es gibt sowohl Erstanbieter Cookies als auch Drittanbieter-Cookies. Erstanbieter-Cookies werden direkt von unserer Seite erstellt, Drittanbieter-Cookies werden von Partner-Webseiten (z.B. Google Analytics) erstellt. Jedes Cookie ist individuell zu bewerten, da jedes Cookie andere Daten speichert. Auch die Ablaufzeit eines Cookies variiert von ein paar Minuten bis hin zu ein paar Jahren. Cookies sind keine Software-Programme und enthalten keine Viren, Trojaner oder andere „Schädlinge“. Cookies können auch nicht auf Informationen Ihres PCs zugreifen.")]),_c('p',[_vm._v("So können zum Beispiel Cookie-Daten aussehen:")]),_c('p',[_c('strong',{staticClass:"adsimple-111680448"},[_vm._v("Name:")]),_vm._v(" _ga"),_c('br'),_c('strong',{staticClass:"adsimple-111680448"},[_vm._v("Wert:")]),_vm._v(" GA1.2.1326744211.152111680448-9"),_c('br'),_c('strong',{staticClass:"adsimple-111680448"},[_vm._v("Verwendungszweck:")]),_vm._v(" Unterscheidung der Webseitenbesucher"),_c('br'),_c('strong',{staticClass:"adsimple-111680448"},[_vm._v("Ablaufdatum:")]),_vm._v(" nach 2 Jahren")]),_c('p',[_vm._v("Diese Mindestgrößen sollte ein Browser unterstützen können:")]),_c('ul',{staticClass:"adsimple-111680448"},[_c('li',{staticClass:"adsimple-111680448"},[_vm._v("Mindestens 4096 Bytes pro Cookie")]),_c('li',{staticClass:"adsimple-111680448"},[_vm._v("Mindestens 50 Cookies pro Domain")]),_c('li',{staticClass:"adsimple-111680448"},[_vm._v("Mindestens 3000 Cookies insgesamt")])]),_c('h3',{staticClass:"adsimple-111680448 fusion-responsive-typography-calculated",staticStyle:{"--fontSize":"21","line-height":"1.5","--minFontSize":"21"},attrs:{"data-fontsize":"21","da