cons kann man sogar manchmal ganze Wörter ersetzen und Platz sparen. Da ist besonders praktisch, wenn wir Inhalte speziell für Smartphones optimieren. Diese Icons werden statt als Bild als HMTL-Code eingefügt. Dadurch können wir die Icons mit CSS genauso bearbeiten, wie wir wollen. Gleichzeitig verbessern wir mit Font Awesome auch unsere Ladegeschwindigkeit, weil es sich nur um HTML-Elemente handelt und nicht um Icon-Bilder. All diese Vorteile helfen uns, die Website für Sie noch übersichtlicher, frischer und schneller zu machen.")]),_c('h3',{staticClass:"adsimple-111680448 fusion-responsive-typography-calculated",staticStyle:{"--fontSize":"21","line-height":"1.5","--minFontSize":"21"},attrs:{"data-fontsize":"21","data-lineheight":"31.5px"}},[_vm._v("Welche Daten werden von Font Awesome gespeichert?")]),_c('p',[_vm._v("Zum Laden von Icons und Symbolen wird das Font Awesome Content Delivery Network (CDN) verwendet. CDNs sind Netzwerke von Servern, die weltweit verteilt sind und es möglich machen, schnell Dateien aus der Nähe zu laden. So werden auch, sobald Sie eine unserer Seiten aufrufen, die entsprechenden Icons von Font Awesome bereitgestellt.")]),_c('p',[_vm._v("Damit die Web-Schriftarten geladen werden können, muss Ihr Browser eine Verbindung zu den Servern des Unternehmens Fonticons, Inc. herstellen. Dabei wird Ihre IP-Adresse erkannt. Font Awesome sammelt auch Daten darüber, welche Icon-Dateien wann heruntergeladen werden. Weiters werden auch technische Daten wie etwa Ihre Browser-Version, Bildschirmauflösung oder der Zeitpunkt der ausgerufenen Seite übertragen.")]),_c('p',[_vm._v("Aus folgenden Gründen werden diese Daten gesammelt und gespeichert:")]),_c('ul',{staticClass:"adsimple-111680448"},[_c('li',{staticClass:"adsimple-111680448"},[_vm._v("um Content Delivery Netzwerke zu optimieren")]),_c('li',{staticClass:"adsimple-111680448"},[_vm._v("um technische Fehler zu erkennen und zu beheben")]),_c('li',{staticClass:"adsimple-111680448"},[_vm._v("um CDNs vor 